import request from '@/common/js/fetch';

//确订单认
export function checkOrder(ids) {
    const data = {
      ids:ids.join(',')
    }
    return request({
      url: '/order/checkout',
      method: 'get',
      // params:{ids:'27_010100110000009515'}
      params:data
    })
  }

  //提交订单
  export function submitOrder(params) {
    return request({
      url: '/order/submit',
      method: 'post',
      params
    })
  }

  //查询类别
  export function getOrderTypes() {
    return request({
      url: '/order/getShowOrderTypes',
      method: 'get',
    })
  }

  //查询订单
  export function getAllOrder(orderStatus,pageNo,pageSize) {
    const data={
      orderStatus,
      pageNo,
      pageSize
    }
    return request({
      url: '/order/getAllOrderByOrderStatus',
      method: 'get',
      params:data
    })
  }

  //申请退货
  export function goRefund(skuCode,orderNo) {
    const data={
      skuCode,
      orderNo
    } 
    return request({
      url: '/order/goRefundPageBySkuAndOrderNo',
      method: 'get',
      params:data
    })
  }


